.o-posterContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 350px;
  margin-bottom: 300px;
  margin-top: 100px;
  position: relative;
}

.o-posterContainer:hover .e-posterIcon .label {
  opacity: .8;
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
}

#e-posterBackground--myPortfolio {
  background: -webkit-linear-gradient(45deg, #ff476f, #fa7e19);
  background: -o-linear-gradient(45deg, #ff476f, #fa7e19);
  background: linear-gradient(45deg, #ff476f, #fa7e19);
}

#e-posterBackground--shopStreetwear {
  background: -webkit-linear-gradient(45deg, #585764, #8c8a9c);
  background: -o-linear-gradient(45deg, #585764, #8c8a9c);
  background: linear-gradient(45deg, #585764, #8c8a9c);
}

.e-posterImage {
  background-size: cover;
  -webkit-box-shadow: 0px 25px 52px -3px #002d759d;
  box-shadow: 0px 25px 52px -3px #002d759d;
  width: 450px;
  height: 350px;
  position: absolute;
  z-index: 2;
  right: 20px;
  top: 100px;
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
}

.e-posterImage--myPortfolio {
  background-image: url(./img/myPortfolio.png);
}

.e-posterImage--StreetwearShop {
  background-image: url(./img/streetwearShop.png);
}

.posterImage--open {
  width: 730px;
  height: 550px;
  top: 30px;
  -webkit-transition: all 400ms;
  -o-transition: all 400ms;
  transition: all 400ms;
}

.e-posterTitle {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  left: 3%;
  top: -70px;
  cursor: pointer;
}

.e-posterTitle h1 {
  margin: 0;
  padding: 0;
  font-size: 30px;
  font-weight: 700;
}

.e-posterTitle .fas {
  font-size: 40px;
  margin-right: 10px;
  -webkit-transform: translateX(-30px);
      -ms-transform: translateX(-30px);
          transform: translateX(-30px);
  opacity: 0.6;
}

.e-posterTitle:hover .fas {
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  opacity: 1;
  -webkit-transition: all 700ms;
  -o-transition: all 700ms;
  transition: all 700ms;
}

.e-posterDescription {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  position: absolute;
  left: 5%;
  margin-top: 20px;
  width: 30%;
