.e-posterDescription p {
  font-family: 'Montserrat', sans-serif;
  color: rgba(255, 255, 255, 0.7);
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 2px;
  line-height: 35px;
}

.e-posterIcon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 20px;
