body {
  margin: 0;
  padding: 0;
  font-family: 'Merriweather', serif;
}

::-moz-selection {
  background: #33aeff;
  color: white;
}

::selection {
  background: #33aeff;
  color: white;
}

::-moz-selection {
  background: #33aeff;
  color: white;
}

