#mobileMenu {
  display: block;
  position: fixed;
  z-index: 3;
  top: 60px;
  height: 100%;
  width: 100%;
  left: -100%;
  background-color: #fff;
  -webkit-transition: all 200ms cubic-bezier(0.41, 0.93, 0.59, 0.96);
  -o-transition: all 200ms cubic-bezier(0.41, 0.93, 0.59, 0.96);
  transition: all 200ms cubic-bezier(0.41, 0.93, 0.59, 0.96);
}

#mobileMenu a {
  color: black;
}

#mobileMenu a:focus {
  color: black;
  text-decoration: none;
}

#mobileMenu a:hover {
  color: black;
  text-decoration: none;
}

#mobileMenu .e-mobileMenu {
  opacity: 0;
}

#mobileMenu .e-mobileMenu span {
  width: 100%;
  display: inline-block;
  text-decoration: none;
  color: #000;
  font-weight: 700;
  opacity: 0;
  -webkit-transition: all 400ms cubic-bezier(0.41, 0.93, 0.59, 0.96);
  -o-transition: all 400ms cubic-bezier(0.41, 0.93, 0.59, 0.96);
  transition: all 400ms cubic-bezier(0.41, 0.93, 0.59, 0.96);
  -webkit-transition-delay: 200ms;
  -o-transition-delay: 200ms;
     transition-delay: 200ms;
  -webkit-transform: translateX(-50px);
  -ms-transform: translateX(-50px);
      transform: translateX(-50px);
  cursor: pointer;
}

.menu-open {
  left: 0 !important;
}

.menu-open .e-mobileMenu {
  opacity: 1 !important;
  position: absolute;
  top: 20vh;
  width: 80%;
  height: 300px;
  line-height: 80px;
  left: 20px;
  font-family: "Karla", serif;
  font-size: 20px;
}

.menu-open .e-mobileMenu span {
  opacity: 1 !important;
  -webkit-transform: translateX(0) !important;
  -ms-transform: translateX(0) !important;
      transform: translateX(0) !important;
}

@media only screen and (max-width: 480px) {
  #mobileMenu {
    display: block;
  }
}


