button:active {
  outline: none !important;
  border: none !important;
}

button:focus {
  outline: 0px !important;
}

main {
  text-align: left;
  min-height: 100vh;
  max-width: 810px;
  padding: 150px 0 0 0;
  margin: 0 auto;
}

main h1 {
  font-family: 'Karla', sans-serif;
  font-size: 30px;
  font-weight: 700;
}

main h2 {
  font-family: 'Karla', sans-serif;
  font-size: 30px;
  font-weight: 700;
}

main h3 {
  font-family: 'Karla', sans-serif;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
}

main h6 {
  font-family: 'Merriweather', 'Karla', sans-serif;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 5px 0 0px 0;
  line-height: 40px;
  color: #7f7f7f;
}

main p {
  font-family: 'Merriweather', 'Karla', sans-serif;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 20px 0 20px 0;
  line-height: 45px;
  color: #7f7f7f;
}

.main--out {
  -webkit-transform: translateX(-25px);
      -ms-transform: translateX(-25px);
          transform: translateX(-25px);
  opacity: 0;
}

.main--in {
  -webkit-transform: translateX(25px);
      -ms-transform: translateX(25px);
          transform: translateX(25px);
  opacity: 0;
}

.main--open {
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  opacity: 1;
  -webkit-transition: all 100ms;
  -o-transition: all 100ms;
  transition: all 100ms;
}

@media only screen and (max-width: 480px) {
  main {
    padding: 100px 0 0 0;
  }

  h1 {
    font-size: 28px !important;
  }

  p {
    font-size: 15px !important;
    line-height: 43px !important;
  }
}


